import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import artpage from '@/components/artpage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'artpage',
      component: artpage
    }
  ]
})



