<template>
    <div>
        <div id="side-menu">
            <div id="developer-name" align="center">
                <span class="color-white">© KimSean</span>
            </div>
            <div align="center">
                <h4 class="color-white"><b>Mangya!</b></h4>
            </div>
            <div>
                <input class="form-control" type="file" id="file"  accept="image/x-png,image/jpeg" @change="processFile()">
                <br>
            </div>
            <div>
                <textarea id="data-input" v-model="myJSON" style="width:100%;resize:none"></textarea>
            </div>
            <div>
                <button style="width:100%" v-on:click="saveData()">Save File</button>
                <button style="width:100%;margin-top:5px;" v-on:click="uploadData()">Upload Data</button>
                <button style="width:100%;margin-top:5px;">New Page</button>
               <!-- <button v-on:click="viewData()">ViewData</button> -->
            </div>
            <div class="color-white" style="font-size:10px;margin-top:10px;">
                 Transparency:<input value="100" type="range" v-on:input="changeTransparency($event)">
                 Font Size:
                 <select v-model="font_size" style="margin-top:5px;" class="form-control" name="" id="">
                     <option style="font-size:8px" value="8">8</option>
                     <option style="font-size:9px" value="9">9</option>
                     <option style="font-size:10px" value="10">10</option>
                     <option style="font-size:11px" value="11">11</option>
                     <option style="font-size:12px" value="12">12</option>
                     <option style="font-size:13px" value="13">13</option>
                     <option style="font-size:14px" value="14">14</option>
                     <option style="font-size:15px" value="15">15</option>
                     <option style="font-size:16px" value="16">16</option>
                     <option style="font-size:17px" value="17">17</option>
                     <option style="font-size:18px" value="18">18</option>
                     <option style="font-size:19px" value="19">19</option>
                     <option style="font-size:20px" value="20">20</option>
                 </select>
            </div>
        </div>
        
        <div id="manga-page" v-on:click="clickPage()" v-on:contextmenu="openMenu($event)" >
            <div 
                v-for="(item, index) in data" :key="index"
                style="background:#fffff;
                position:absolute;padding:19px 0px 0px 0px;
                border:3px solid red" 
                v-bind:style="{ top: item[0].y_location+'px',left:item[0].x_location+'px',width:item[0].width+'px',height:item[0].height+'px'}"
                v-bind:id="item[0].elem_id"
                v-bind:myIndex="index"
                class="page-comments-container ui-widget-content"
                >

                <textarea @input="updateComment(index,$event)"
                 style="background:#fffff;color:black;
                 border-top:3px solid red;
                 resize:none;width:100%;height:100%;"
                  v-bind:style="{ fontSize:font_size +'px'}"
                 class="comment-input" >{{item[0].details}}</textarea>
            </div>

             <div id="options">
                <button v-on:click="addComment($event)" style="background:#97C664;border:none;color:#ffff;padding:5px 8px 5px 8px">
                    Add Comment
                </button>
            </div>
        </div>
    </div>
</template>
<script>


export default {
    data () {
        return {
        msg: 'Welcome to artpage',
        data:[],
        myJSON:'',
        jsonString:'',
        image64:'',
        font_size:12
        }
    },
    methods:{
        getBase64Image:function(file){
            var reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = function () {
                $('#manga-page').css('background-image', 'url(' + reader.result + ')');
            };
            reader.onerror = function (error) {
                console.log('Error: ', error);
            };
        },
        processFile:function(){
            var files = document.getElementById('file').files;
            this.getBase64Image(files[0]);
        },
        changeTransparency:function($event){
            var curr_value = $event.target.value;
            var new_val = curr_value / 100;
            $(".page-comments-container").css("opacity",new_val)
        },
        clickPage:function(){
            
            $("#options").css({"visibility":"hidden",});
            var vm = this.data;
            $(".page-comments-container").draggable({
                    containment: "#manga-page",
                    stop: function(event, ui) {
                        var pos = ui.helper.position(); // just get pos.top and pos.left
                        var div_id = event.target.id;
                        var index = $("#"+div_id).attr('myIndex');
                            vm[index][0]['x_location'] = pos.left;
                            vm[index][0]['y_location'] = pos.top;
                    }
            });

            $( ".page-comments-container").resizable({
                 resize:function(event, ui){
                    var width = ui.size.width;
                    var height = ui.size.height
                 },
                 stop: function(event, ui) {
                    var width = ui.size.width;
                    var height = ui.size.height;
                    var div_id = event.target.id;
                    var index = $("#"+div_id).attr('myIndex');
                        vm[index][0]['width'] = width;
                        vm[index][0]['height'] = height;
                 }
            });

        },
        uploadData:function(){
            if(this.myJSON){
                this.data = JSON.parse(this.myJSON)
            }else{
                console.log("error");
            }
            
        },
        makeId:function(){
            var text = "";
            var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
            for (var i = 0; i < 20; i++)
                text += possible.charAt(Math.floor(Math.random() * possible.length));
            return text;
        },
        updateComment:function(index,$event){
            this.data[index][0]['details'] = $event.target.value;
        },
        openMenu:function($event){
            var parentOffset = $("#manga-page").offset(); 
            this.selected_x = $event.pageX - parentOffset.left;
            this.selected_y = $event.pageY - parentOffset.top;
            
            $("#options").css({
				   	"top":this.selected_y,
				   	"left":this.selected_x,
				   	"visibility":"visible",
                   });
            $event.preventDefault();
        },
        saveData:function($event){
            this.myJSON = JSON.stringify(this.data);
            setTimeout(function(){
                var copyText = document.getElementById("data-input");
                /* Select the text field */
                copyText.select();
                /* Copy the text inside the text field */
                document.execCommand("copy");
                /* Alert the copied text */
                alert("Copied the text");
            }, 1000);
        },
        addComment:function($event){
            this.clickPage();
            var parentOffset = $("#manga-page").offset(); 
            this.selected_x = $event.pageX - parentOffset.left;
            this.selected_y = $event.pageY - parentOffset.top;

            var details = [{
                x_location:this.selected_x,
                y_location:this.selected_y,
                elem_id:this.makeId(),
                width:120,
                height:130
            }]
            this.data.push(details);
            $("#options").css({"visibility":"hidden",});

            $(".page-comments-container").draggable({
                    containment: "#manga-page",
                    stop: function(event, ui) {
                        var pos = ui.helper.position(); // just get pos.top and pos.left
                        var div_id = event.target.id;
                    }
            });
        }
    },
}
</script>
<style scoped>
#developer-name{
    position: absolute;
    bottom: 10px;
    margin: auto;
    left: 0;
    right: 0;
}
.color-white{
    color: #ffffff;
}
#side-menu{
    width: 200px;
    height: 100%;
    position: fixed;
    background:#2A3F54;
    padding: 10px;
    z-index: 1000;
}
#options{
	position: absolute;
    background: #32404E;
	padding: 5px;
    visibility: hidden;
    z-index: 2000;
}
#manga-page{
    width: 900px;
    height: 1300px;
    background:rgb(204, 203, 203);
    margin-left: 210px;
    background-size: 100% 100%;
    position: relative;
}
</style>



